@use "../../../styles/utils" as *;

.btnMain {
  &:hover {
    background: $red;
  }
}

.btnInfo {
  &:hover {
    background: #01d3e0;
  }
}

.btnSuccess {
  &:hover {
    background: $blue;
  }
}

@media screen and (max-width: 450px) {
  .btnMain {
    background: $red;
  }
  .btnInfo {
    background: #01d3e0;
  }
  .btnSuccess {
    background: $blue;
  }
}
